/**
* @author William Cui
* @description 桌台信息窗
* @date 2017-03-30
**/
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { browserHistory } from 'react-router';
import { message, Menu, Dropdown, Tabs } from 'antd';
import classnames from 'classnames';

import PromptNumber from './prompt-number';
import PromptPopup from 'components/prompt-popup';
import SelectWaiterPopup from './select-waiter-popup';
import Scrollbars from 'react-custom-scrollbars';
import CancelTablePopup from './cancel-table-popup';
import TurnTablePopup from './change-desk-dishes-popup';
import CancelBookingPopup from './cancel-booking-reason-popup';
import OpenClassPopup from '../app-popup/open-class-popup'; //开班

const TabPane = Tabs.TabPane;

const promptContStyle = {
  lineHeight: '200px',
  textAlign: 'center',
  color: '#000',
  fontSize: '20px'
}
@inject('appStore', 'dineStore') @observer
class TableWindow extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      numberKeyBoard: false,
      statePopup: false,
      customerNumber: '',
      transferType: 1,
      orderKey: '0'
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      orderKey: '0'
    });
  }

  componentDidMount() {
    let { appStore, dineStore, bookingID } = this.props;
    
    if(bookingID) {
      //如果有bookingID，那么就是预定开台
      if(!appStore.isDayEnd) {
        this.checkDayEnd();
      }else {
        dineStore.bookingOpenTableView({bookingID});
        //重置操作服务员
        dineStore.resetOperateWaiter();
      }
    }
  }

  checkDayEnd() {
    //上一营业日没有日结
    this.setState({
      statePopup: <PromptPopup 
        okText="去日结" 
        cancelText="知道了"
        onOk={() => {
          this.context.router.push('/day-end');
          this.setState({statePopup: false});
        }}
        onCancel={() => {
          this.setState({statePopup: false});
        }}
      >
        <div style={promptContStyle}>上一营业日还没日志</div>
      </PromptPopup>
    });
  }

  checkOpenClass() {
    this.setState({
      statePopup: <PromptPopup 
        okText="去开班" 
        cancelText="知道了"
        onOk={() => { 
          this.setState({
            statePopup: <OpenClassPopup 
                closeCancel={()=>{
                    this.setState({statePopup: false}); 
                }}
                okCancel={()=>{
                    this.setState({statePopup: false}); 
                }}
            >
            </OpenClassPopup>
          })
        }}
        onCancel={() => {
          this.setState({statePopup: false});
        }}
      >
        <div style={promptContStyle}>开班后才能开台</div>
      </PromptPopup>
    })
  }


  render() {
    let { dineStore, appStore } = this.props;
    let selectDesk,relateDesk,targetDesk,
    orderDetail,operateWaiter,
    waveLine,operateButtons=[],moreButtons=[],operate,moreDropDown;

    orderDetail = <div className="order_detail_pic">
        <div className="desk-pic"><i className="iconfont icon-zhuotaixinxi_icon_kongtai-"></i></div>
    </div>;

    if(dineStore.selectedTableList.length && dineStore.selectedTableList[0].tableStatus) {

      let isCombin = dineStore.selectedTableList[0].combineNumber > 1;
      let selectedTable = dineStore.selectedTableList[0];

      let detailTemp=<div className="order_detail">
        <Tabs 
          activeKey={this.state.orderKey}
          onChange={(key) => {
            this.setState({
              orderKey: key
            });
          }}
          tabBarStyle={{
            display: isCombin ? 'block' : 'none'
          }}
        > 
          <TabPane tab="子订单" key="0">
            <div className={classnames({
              'or-detail-content': true,
              'or-no-tab': !isCombin
            })}>
              {isCombin && <div className="order-one-line"><span className="order-title">桌台：</span><span className="order-id">{selectedTable.tableName}</span></div>}
              <div className="order-one-line"><span className="order-title">{isCombin && '子'}订单号：</span><span className="order-id">{selectedTable.orderCode}</span></div>
              <div className="order-normal"><span className="order-title">状态：</span>{((tableStatus) => {
                let status;
                switch(tableStatus) {
                  case 606:
                    status = '已预订'
                    break;
                  case 827: 
                    status = '未下单';
                    break;
                  case 652:
                    status = '已下单';
                    break;
                  default: 
                    status = '已暂结'
                }
                return status;
              })(selectedTable.tableStatus)}</div>
              <div className="order-normal"><span className="order-title">{isCombin && '子'}订单金额：</span>{selectedTable.subTotalAmount}元</div>
              <div className="order-normal"><span className="order-title">就餐人数：</span>{selectedTable.subPeopleNum}</div>
              <div className="order-normal"><span className="order-title">已上菜：</span>{selectedTable.subServedNum}</div>
              <div className="order-normal"><span className="order-title">未上菜：</span>{selectedTable.subUnServedNum}</div>
              {selectedTable.memo && <div className="order-remarks">
                <div className="order-title">备注：</div>
                <div className="remark-content">{selectedTable.memo}</div>
              </div>}
            </div>
          </TabPane>
          {isCombin && <TabPane tab="总订单" key="1">
            {<div className="or-detail-content">
              <div className="order-one-line">
                <span className="order-title">桌台：</span>
                <span className="order-id">
                  {selectedTable.tableName}, {selectedTable.combineTableInfoList.map(table => {
                    return table.tableName;
                  }).join(', ')}
                </span>
              </div>
              <div className="order-one-line"><span className="order-title">订单号：</span><span className="order-id">{selectedTable.orderCode}</span></div>
              <div className="order-normal"><span className="order-title">状态：</span>{((tableStatus) => {
                let status;
                switch(tableStatus) {
                  case 606:
                    status = '已预订'
                    break;
                  case 827: 
                    status = '未下单';
                    break;
                  case 652:
                    status = '已下单';
                    break;
                  default: 
                    status = '已暂结'
                }
                return status;
              })(selectedTable.tableStatus)}</div>
              <div className="order-normal"><span className="order-title">订单总金额：</span>{selectedTable.totalAmount}</div>
              <div className="order-normal"><span className="order-title">就餐人数：</span>{selectedTable.peopleNum}</div>
              <div className="order-normal"><span className="order-title">已上菜：</span>{selectedTable.servedNum}</div>
              <div className="order-normal"><span className="order-title">未上菜：</span>{selectedTable.unServedNum}</div>
              {selectedTable.memo && 
              <div className="order-remarks">
                <div className="order-title">备注：</div>
                <div className="remark-content">{selectedTable.memo}</div>
              </div>}
            </div>}
          </TabPane>}
        </Tabs>
      </div>

      switch (dineStore.selectedTableList[0].tableStatus) {
        case 607:
          //空闲
          operateButtons =[{
            text: '开台'
          },{
            text: '维修'
          }];
          break;
        case 606:
          //预定
          operateButtons=[{
            text: '预订开台'
          },{
            text: '取消预订'
          }];
          orderDetail=<div className="order_detail or-no-tab">
            <div className="or-detail-content">
              <div className="order-one-line"><span className="order-title">预订单号：</span><span className="order-id">{selectedTable.orderCode}</span></div>
              <div className="order-normal"><span className="order-title">预定人：</span>{selectedTable.contact}</div>
              <div className="order-one-line"><span className="order-title">预定人电话：</span>{selectedTable.phone}</div>
              <div className="order-normal"><span className="order-title">状态：</span>{((tableStatus) => {
                let status;
                switch(tableStatus) {
                  case 606:
                    status = '已预订'
                    break;
                  case 827: 
                    status = '未下单';
                    break;
                  case 652:
                    status = '已下单';
                    break;
                  default: 
                    status = '已暂结'
                }
                return status;
              })(selectedTable.tableStatus)}</div>
              <div className="order-normal"><span className="order-title">就餐人数：</span>{selectedTable.peopleNum}</div>
              <div className="order-normal"><span className="order-title">就餐时间：</span>{selectedTable.arrivalTime}</div>
              <div className="order-normal"><span className="order-title">预定方式：</span>{selectedTable.bookingType}</div>
              <div className="order-one-line"><span className="order-title">预定时间：</span>{selectedTable.bookingTime}</div>
              <div className="order-normal"><span className="order-title">预付金额：</span>{selectedTable.bookingAmount}</div>
              {selectedTable.memo && <div className="order-remarks">
                <div className="order-title">备注：</div>
                <div className="remark-content">{selectedTable.memo}</div>
              </div>}
            </div>
          </div>
          break;
        case 827:
          //未下单
          operateButtons=[{
            text: '点菜'
          },{
            text: '转台'
          }];

          //是否可拼台
          if(dineStore.selectedTableList[0].canSharing) {
            operateButtons.push({
              text: '拼台'
            });
            moreButtons.push({
              text: '消台'
            });
          }else {
            operateButtons.push({
              text: '消台'
            });
          }

          operateButtons.push({
            text: '更多',
            weakened: true
          });

          moreButtons.push({
            text: '加台'
          });

          //是否可联台
          dineStore.selectedTableList[0].canCombine &&
          moreButtons.push({
            text: '联台'
          });

          //是否可拆台
          dineStore.selectedTableList[0].canSplit &&
          dineStore.relationTableList.length &&
          moreButtons.push({
            text: '拆台'
          });

          orderDetail = detailTemp;
          break;
        case 652:
          //已下单
          operateButtons=[{
            text: '加菜'
          },{
            text: '暂结'
          },{
            text: '结账'
          },{
            text: '更多',
            weakened: true
          }];

          moreButtons=[{
            text: '转台'
          },{
            text:'加台'
          }];

          //是否可拼台
          dineStore.selectedTableList[0].canSharing &&
          moreButtons.push({
            text: '拼台'
          });

          //是否可联台
          dineStore.selectedTableList[0].canCombine &&
          moreButtons.push({
            text: '联台'
          });

          //是否可拆台
          dineStore.selectedTableList[0].canSplit &&
          dineStore.relationTableList.length &&
          moreButtons.push({
            text: '拆台'
          });
          moreButtons.push({
            text: '打印小票'
          });
          orderDetail = detailTemp;
          break;
        case 728:
          //暂结
          operateButtons=[{
            text: '取消暂结'
          },{
            text: '结账'
          }];
          orderDetail = detailTemp;
          break;
        case 609:
          //脏台
          operateButtons=[{
            text: '清台'
          }];
          break;
        case 610:
          //维修
          operateButtons=[{
            text: '恢复'
          }];
          break;
        default:
      }

      const operateHandle = function({ key }) {
        switch(key) {
          case '开台':
            if(!appStore.isDayEnd) {
              this.checkDayEnd();
            }else {
              dineStore.changeTableWindow('openTable');
              //重置操作服务员
              dineStore.resetOperateWaiter();
            }
            break;
          case '预订开台':
            if(!appStore.isDayEnd) {
              this.checkDayEnd();
            }else {
              dineStore.bookingOpenTableView({
                bookingID: dineStore.selectedTableList[0].bookingID
              });
              //重置操作服务员
              dineStore.resetOperateWaiter();
            }
            break;
          case '取消预订':
            this.setState({
              statePopup: <CancelBookingPopup
                handelClose={() => {
                  this.setState({statePopup: false});
                }}
              />
            });
            break;
          case '点菜':
            dineStore.saveCache();
            browserHistory.push('/dishes/'+dineStore.selectedTableList[0].tableID+'/'+dineStore.selectedTableList[0].subOrderID);
            break;
          case '加菜':
            dineStore.saveCache();
            browserHistory.push('/dishes/'+dineStore.selectedTableList[0].tableID+'/'+dineStore.selectedTableList[0].subOrderID);
            break;
          case '转台':
            dineStore.transferTableView();
            break;
          case '拼台':
            if(!appStore.isDayEnd) {
              this.checkDayEnd();
            }else {
              dineStore.changeTableWindow('shareTable');
              //重置操作服务员
              dineStore.resetOperateWaiter();
            }
            break;
          case '消台':
            if(dineStore.relationTableList.length > 0) {
              //切换到批量消台视图
              dineStore.cancelTableView();
            }else {    
              //单个消台            
              this.setState({
                statePopup: <CancelTablePopup 
                  handleClose={() => {
                    this.setState({statePopup: false});
                  }}
                />
              });
            }
            break;
          case '加台':
            if(!appStore.isDayEnd) {
              this.checkDayEnd();
            }else {
              dineStore.addTableView();
            }
            break;
          case '联台':
            dineStore.combineTableView();
            break;
          case '拆台':
            dineStore.splitTableView();
            break;
          case '暂结':
            dineStore.validateSettlement({
              type: 'temporarily',
              subOrderID: dineStore.selectedTableList[0].subOrderID
            });
            break;
          case '取消暂结':
            dineStore.temporarily(dineStore.selectedTableList[0].subOrderID);
            break;
          case '结账':
            if(!appStore.isOpenClass) {
              this.checkOpenClass();
            }else {
              dineStore.validateSettlement({
                type: 'settlement',
                subOrderID: dineStore.selectedTableList[0].subOrderID
              });
            }
            break;
          case '清台':
            dineStore.clearTable(dineStore.selectedTableList.map((table) => {
              return table.tableID;
            }));
            break;
          case '打印小票':
            browserHistory.push('/small-ticket/'+dineStore.selectedTableList[0].subOrderID);
            break;
          default:
        }
      }

      operate=<div className="desk_operate">
        {operateButtons.map((button,index) => {
          let className=button.weakened ? 'btn-normal' : 'btn-active';
          let buttonBlock;
          if(button.text==='更多') {
             moreDropDown = <Menu onClick={operateHandle.bind(this)}>
              {moreButtons.map((moreButton,index) => {
                return <Menu.Item key={moreButton.text}>{moreButton.text}</Menu.Item>
              })}
            </Menu>;
            buttonBlock = <Dropdown key={index} overlay={moreDropDown} placement="topCenter">
              <div className={className}>{button.text}</div>
            </Dropdown>
          }else {
            buttonBlock = <div 
              key={index} 
              className={className} 
              onClick={operateHandle.bind(this,{key: button.text})}
            >
              {button.text}
            </div>
          }
          return buttonBlock;
        })}
      </div>

      if(dineStore.relationTableList.length) {
        relateDesk= <div className="relateDesk">
        <h5>关联桌台</h5>
        {dineStore.relationTableList.map((table,index) =>{
          return <div key={index} className="has-select-content">
          <span className={classnames({
            'has-select-point': true,
            'point-reserve': table.tableStatus === 606, //预定
            'point-not-order': table.tableStatus === 827, //未下单
            'point-has-order': table.tableStatus === 652, //已下单
            'point-zanjie': table.tableStatus === 728, //暂结
            'point-dirty': table.tableStatus === 609, //脏台
            'point-free': table.tableStatus === 607, //空闲
            'point-service': table.tableStatus === 610, //维修
          })}></span>
          <span className="has-select-number">
            {table.shareTableName && table.tableName !== table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName}
            ({table.customerNumber ? table.customerNumber : 0 }/{table.defaultPerson})
          </span>
          <span className="has-select-location">{table.areaName}</span>
          <span className="has-select-name">{table.nickName}</span>
          </div>
        })}
        </div>
      }

      if(dineStore.targetTableList.length) {
          targetDesk = <div className="target-container">
            <Scrollbars>
              {(() => {
                return dineStore.targetTableList.map((table,targetIndex) =>{
                  return <div key={targetIndex} className="has-select-content">
                    <span className={classnames({
                      'has-select-point': true,
                      'point-reserve': table.tableStatus === 606, //预定
                      'point-not-order': table.tableStatus === 827, //未下单
                      'point-has-order': table.tableStatus === 652, //已下单
                      'point-zanjie': table.tableStatus === 728, //暂结
                      'point-dirty': table.tableStatus === 609, //脏台
                      'point-free': table.tableStatus === 607, //空闲
                      'point-service': table.tableStatus === 610, //维修
                    })}></span>
                    <span className="has-select-number">
                      {table.shareTableName && table.tableName !== table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName}
                      ({table.customerNumber ? table.customerNumber : 0 }/{table.defaultPerson})
                    </span>
                    <span className="has-select-location">{table.areaName}</span>
                    <span className="has-select-name">{table.nickName}</span>
                  </div>
              });
            })()}
          </Scrollbars>
        </div>
      }

      selectDesk=<div className="choose_desk">
        <Scrollbars>
          <div className="hasSelectDesk">
            <h5>{dineStore.targetView ? '原桌台' : '已选桌台'}</h5>
            {(() => {
              return dineStore.selectedTableList.map((table,selectedIndex) =>{
                  return <div key={selectedIndex} className="has-select-content">
                    <span className={classnames({
                      'has-select-point': true,
                      'point-reserve': table.tableStatus === 606, //预定
                      'point-not-order': table.tableStatus === 827, //未下单
                      'point-has-order': table.tableStatus === 652, //已下单
                      'point-zanjie': table.tableStatus === 728, //暂结
                      'point-dirty': table.tableStatus === 609, //脏台
                      'point-free': table.tableStatus === 607, //空闲
                      'point-service': table.tableStatus === 610, //维修
                    })}></span>
                    <span className="has-select-number">
                      {table.shareTableName && table.tableName !== table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName}
                      ({table.customerNumber ? table.customerNumber : 0 }/{table.defaultPerson})
                    </span>
                    <span className="has-select-location">{table.areaName}</span>
                    <span className="has-select-name">{table.nickName}</span>
                  </div>
              });
            })()}
          </div>
          {relateDesk}
        </Scrollbars>
      </div>

      operateWaiter=<div className="kt-person" onClick={() => {
          dineStore.getWaiterList();
          this.setState({statePopup: <SelectWaiterPopup okClick={(waiter) => {
            this.setState({statePopup: false});
          }} cancelClick={(e) => {
            this.setState({statePopup: false});
          }}/>});
        }}>
        <div>服务员</div>
        <div className="person-name">
          {dineStore.operateWaiter.userName ? dineStore.operateWaiter.userName : JSON.parse(sessionStorage.getItem('account')).userName}
          <i className="iconfont icon-home_title_arrow_right"></i>
        </div>
        {this.state.statePopup}
      </div>

      waveLine=<div className="wave-line"></div>;
    }

    if(dineStore.tableWindow !== 'shareTable') this.state.customerNumber = ''; //重置customerNumber为''
    if(dineStore.tableWindow !== 'transferTable') this.state.transferType = 1; //重置transferType为 1

    return <div>
      {(() => {
          if(dineStore.selectedTableList.length) {
            if(dineStore.tableWindow === 'openTable') {
              //开台操作窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">开台</h4>
                <div className="kai-input-block">
                <Scrollbars>
                    {dineStore.selectedTableList.map((table,index) =>{
                     return <div key={index} className="kt-input-num">
                         <div className="kt-name">{table.tableName}</div>
                         <div className="kt-number">
                             <span>就餐人数</span>
                             <input type="text" value={table.customerNumber ? table.customerNumber : ''} onClick={() =>{
                               this.setState({numberKeyBoard: <PromptNumber okClick={(customerNumber) => {
                                 if(customerNumber === '0' ) {
                                   message.warn('就餐人数不能为零！');
                                   return;
                                 }
                                 if(!customerNumber) {
                                   message.warn('就餐人数不能为空！');
                                   return;
                                 }
                                 dineStore.enterCustomerNumber(table.tableID, customerNumber);
                                 this.setState({numberKeyBoard: false});
                               }} cancelClick={(e) => {
                                 this.setState({numberKeyBoard: false});
                               }} />});
                             }} readOnly />
                         </div>
                     </div>
                    })}
                    {this.state.numberKeyBoard}
                    {operateWaiter}
                  </Scrollbars>
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.changeTableWindow('base');
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      let tableIDs=[];
                      let numbers=[];
                      dineStore.selectedTableList.forEach((table) =>{
                        table.tableID && tableIDs.push(table.tableID);
                        table.customerNumber && numbers.push(table.customerNumber);
                      });
                      if(tableIDs.length === numbers.length) {
                        let openTableData={
                          'tableIDs[]': tableIDs,
                          'customerNumbers[]': numbers
                        }
                        //如果选择了操作服务员就追加loginID
                        if(dineStore.operateWaiter.loginID) openTableData.loginID=dineStore.operateWaiter.loginID;
                        dineStore.openTable(openTableData);
                      }else {
                        message.info('就餐人数不能为空');
                      }
                    }}>确定</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'bookingOpenTable') {
              //预定开台操作窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">预定开台</h4>
                <div className="kai-input-block">
                <Scrollbars>
                    {dineStore.targetTableList.map((table,index) =>{
                     return <div key={index} className="kt-input-num">
                         <div className="kt-name">{table.tableName}</div>
                         <div className="kt-number">
                             <span>就餐人数</span>
                             <input type="text" value={table.customerNumber ? table.customerNumber : ''} onClick={() =>{
                               this.setState({numberKeyBoard: <PromptNumber okClick={(customerNumber) => {
                                 if(customerNumber === '0' ) {
                                   message.warn('就餐人数不能为零！');
                                   return;
                                 }
                                 if(!customerNumber) {
                                   message.warn('就餐人数不能为空！');
                                   return;
                                 }
                                 dineStore.enterCustomerNumber(table.tableID, customerNumber);
                                 this.setState({numberKeyBoard: false});
                               }} cancelClick={(e) => {
                                 this.setState({numberKeyBoard: false});
                               }} />});
                             }} readOnly />
                         </div>
                     </div>
                    })}
                    {this.state.numberKeyBoard}
                    {operateWaiter}
                  </Scrollbars>
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    {!!dineStore.tableList.filter((table) => {
                      return table.tableStatus !== 606;
                    }).length && 
                    <div className="btn-active" onClick={() => {
                      dineStore.bookingExchangeTableView();
                    }}>换台</div>}
                    <div className="btn-active" onClick={() => {
                      let tableIDs=[];
                      let numbers=[];
                      let loginID;
                      let bookingID = dineStore.selectedTableList[0].bookingID;
                      dineStore.targetTableList.forEach((table) =>{
                        table.tableID && tableIDs.push(table.tableID);
                        table.customerNumber && numbers.push(table.customerNumber);
                      });
                      if(tableIDs.length === numbers.length) {
                        //如果选择了操作服务员就追加loginID
                        if(dineStore.operateWaiter.loginID) loginID=dineStore.operateWaiter.loginID;
                        dineStore.bookingOpenTable({
                          bookingID, 
                          tableIDs, 
                          customerNumbers: numbers, 
                          loginID
                        });
                      }else {
                        message.info('就餐人数不能为空');
                      }
                    }}>开台</div>
                    <div className="btn-active" onClick={() => {
                      
                    }}>开台并点菜</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'bookingExchangeTable') {
              //预定换台操作窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">预订换台</h4>
                <div className="choose_desk">
                <Scrollbars>
                  <div className="hasSelectDesk">
                    <h5>被占用桌台</h5>
                    {(() => {
                      return dineStore.occupiedTableList.map((table, index) =>{
                          return <div key={index} className="has-select-content">
                            <span className={classnames({
                              'has-select-point': true,
                              'point-reserve': table.tableStatus === 606, //预定
                              'point-not-order': table.tableStatus === 827, //未下单
                              'point-has-order': table.tableStatus === 652, //已下单
                              'point-zanjie': table.tableStatus === 728, //暂结
                              'point-dirty': table.tableStatus === 609, //脏台
                              'point-free': table.tableStatus === 607, //空闲
                              'point-service': table.tableStatus === 610, //维修
                            })}></span>
                            <span className="has-select-number">
                              {table.shareTableName && table.tableName !== table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName}
                              ({table.customerNumber ? table.customerNumber : 0 }/{table.defaultPerson})
                            </span>
                            <span className="has-select-location">{table.areaName}</span>
                            <span className="has-select-name">{table.nickName}</span>
                          </div>
                      });
                    })()}
                  </div>
                </Scrollbars>
              </div>
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>以下是需要换成的空闲桌台</div>
                    {targetDesk}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      
                      dineStore.turnTableProductList({
                        bookingID: dineStore.occupiedTableList[0].bookingID, 
                        tableIDs: dineStore.occupiedTableList.map(table => {
                          return table.tableID
                        }),
                        handleTurnTable: (function() {
                          this.setState({
                            statePopup: <TurnTablePopup closeHandle={() => {
                              this.setState({statePopup: false});
                            }} />
                          });
                        }).bind(this)
                      })
                    }}>确定</div>
                    {this.state.statePopup}
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'cancelTable') {
              //批量消台窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">消台</h4>
                {selectDesk}
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>下方已经列出全部与{dineStore.selectedTableList[0].tableName}相关且未下单的桌台</div>
                    {targetDesk}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      this.setState({
                        statePopup: <CancelTablePopup 
                          handelClosePopup={() => {
                            this.setState({statePopup: false});
                          }}
                        />
                      });
                    }}>确定</div>
                </div>
                {this.state.statePopup}
              </div>
            }

            if(dineStore.tableWindow === 'addTable01') {
              //加台窗口一
              return <div id="desk_info_container">
                <h4 id="desk_info_header">加台</h4>
                {selectDesk}
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>只能选择处于空闲状态桌台。</div>
                    {targetDesk}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      if(dineStore.targetTableList.length) {
                        dineStore.changeTableWindow('addTable02');
                        //重置操作服务员
                        dineStore.resetOperateWaiter();
                      }else {
                        message.info('未选择目标桌台!');
                      }
                    }}>确定</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'addTable02') {
              //加台窗口二
              return <div id="desk_info_container">
                <h4 id="desk_info_header">加台</h4>
                <div className="kai-input-block">
                  <Scrollbars>
                    <h4 className="kai-input-title">开台人数</h4>
                    {dineStore.targetTableList.map((table,index) =>{
                     return <div key={index} className="kt-input-num">
                         <div className="kt-name">{table.tableName}</div>
                         <div className="kt-number">
                             <span>就餐人数</span>
                             <input type="text" value={table.customerNumber ? table.customerNumber : ''} onClick={() =>{
                               this.setState({numberKeyBoard: <PromptNumber okClick={(customerNumber) => {
                                 if(customerNumber === '0' ) {
                                   message.warn('就餐人数不能为零！');
                                   return;
                                 }
                                 if(!customerNumber) {
                                   message.warn('就餐人数不能为空！');
                                   return;
                                 }
                                 dineStore.enterCustomerNumber(table.tableID, customerNumber);
                                 this.setState({numberKeyBoard: false});
                               }} cancelClick={(e) => {
                                 this.setState({numberKeyBoard: false});
                               }} />});
                             }} readOnly />
                         </div>
                     </div>
                    })}
                    {this.state.numberKeyBoard}
                    {operateWaiter}
                  </Scrollbars>
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.changeTableWindow('addTable01');
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      let tableIDs=[];
                      let numbers=[];
                      dineStore.targetTableList.forEach((table, index) =>{
                        table.tableID && tableIDs.push(table.tableID);
                        table.customerNumber && numbers.push(table.customerNumber);
                      });
                      if(tableIDs.length === numbers.length) {
                        let addTableData={
                          'mainTableID': dineStore.selectedTableList[0].tableID,
                          'viceTableIDs[]': tableIDs,
                          'customerNumbers[]': numbers
                        };
                        //如果选择了操作服务员就追加loginID
                        if(dineStore.operateWaiter.loginID) addTableData.loginID=dineStore.operateWaiter.loginID;
                        dineStore.addTable(addTableData);
                      }else {
                        message.info('就餐人数不能为空');
                      }
                    }}>开台</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'transferTable') {
              //转台窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">转台</h4>
                {selectDesk}
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>请选择一个处于空闲、未下单或已下单状态的桌台。</div>
                    {targetDesk}
                    {(() => {
                      if(dineStore.targetTableList.length && dineStore.targetTableList[0].tableStatus !== 607) {
                        return <div>
                          <div className="select-type">请选择转台方式</div>
                          <div className="tai-style" onClick={() => {
                            this.setState({transferType: 3});
                          }}>
                            <h5>合台</h5>
                            <p>与{dineStore.selectedTableList[0].tableName}桌合并，一起结账</p>
                            <span className={classnames({'has-select-dui': true, 'dui-active': this.state.transferType === 3})}><i className="iconfont icon-kaitairen_icon_sel-"></i></span>
                          </div>

                          <div className="tai-style" onClick={() => {
                            this.setState({transferType: 2});
                          }}>
                            <h5>拼台</h5>
                            <p>与{dineStore.selectedTableList[0].tableName}桌拼台，单独结账</p>
                            <span className={classnames({'has-select-dui': true, 'dui-active': this.state.transferType === 2})}><i className="iconfont icon-kaitairen_icon_sel-"></i></span>
                          </div>
                        </div>
                      }
                    })()}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      if(!dineStore.targetTableList.length) {
                        message.info('未选择目标桌台！');
                        return;
                      }
                      if(dineStore.targetTableList[0].tableStatus !== 607 && this.state.transferType === 1) {
                        message.info('请选择转台方式！');
                        return;
                      }
                      let transferTableData={
                        tableID: dineStore.selectedTableList[0].tableID,
                        subOrderID: dineStore.selectedTableList[0].subOrderID,
                        targetTableID: dineStore.targetTableList[0].tableID,
                        targetSubOrderID: dineStore.targetTableList[0].subOrderID,
                        transferType: this.state.transferType
                      }
                      dineStore.transferTable(transferTableData);
                    }}>确定</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'combineTable') {
              //联台窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">联台</h4>
                {selectDesk}
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>请选择处于未下单或已下单状态的桌台。</div>
                    {targetDesk}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      if(dineStore.targetTableList.length) {
                        let targetTableIDs=[];
                        dineStore.targetTableList.forEach((table) => {
                          targetTableIDs.push(table.tableID);
                        })
                        let combineTableData={
                          tableID: dineStore.selectedTableList[0].tableID,
                          targetTableIDs: targetTableIDs
                        }
                        dineStore.combineTable(combineTableData);
                      }else {
                        message.info('未选择目标桌台！');
                      }
                    }}>确定</div>
                </div>
              </div>
            }

            if(dineStore.tableWindow === 'splitTable') {
              //拆台窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">拆台</h4>
                {selectDesk}
                <div className="targetDesk">
                    <h5>目标桌台</h5>
                    <div className="target-danger"><span className="point-danger">!</span>下方已经列出全部与{dineStore.selectedTableList[0].tableName}相关桌台</div>
                    {targetDesk}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.returnCache();
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      let targetTableIDs=[];
                      dineStore.targetTableList.forEach((table) => {
                        targetTableIDs.push(table.tableID);
                      })
                      let splitTableData={
                        targetTableIDs: targetTableIDs
                      }
                      dineStore.splitTable(splitTableData);
                    }}>确定</div>
                </div>
              </div>
            }
            if(dineStore.tableWindow === 'shareTable') {
              //拼台窗口
              return <div id="desk_info_container">
                <h4 id="desk_info_header">拼台</h4>
                {selectDesk}
                <div className="kai-input-block">
                  <div className="kt-input-num">
                      <div className="kt-name">{dineStore.selectedTableList[0].tableName}</div>
                      <div className="kt-number">
                          <span>就餐人数</span>
                          <input type="text" value={this.state.customerNumber ? this.state.customerNumber : ''} onClick={() =>{
                            this.setState({numberKeyBoard: <PromptNumber okClick={(customerNumber) => {
                              if(customerNumber === '0' ) {
                                message.warn('就餐人数不能为零！');
                                return;
                              }
                              if(!customerNumber) {
                                message.warn('就餐人数不能为空！');
                                return;
                              }
                              this.setState({
                                numberKeyBoard: false,
                                customerNumber: customerNumber
                              });
                            }} cancelClick={(e) => {
                              this.setState({numberKeyBoard: false});
                            }} />});
                          }} readOnly />
                      </div>
                      {this.state.numberKeyBoard}
                  </div>
                  {operateWaiter}
                </div>
                {waveLine}
                <div className="desk_operate">
                    <div className="btn-normal" onClick={() => {
                      dineStore.changeTableWindow('base');
                    }}>取消</div>
                    <div className="btn-active" onClick={() => {
                      if(this.state.customerNumber) {
                        let shareTableData={
                          tableID: dineStore.selectedTableList[0].tableID,
                          customerNumber: this.state.customerNumber
                        }
                        //如果选择了操作服务员就追加loginID
                        if(dineStore.operateWaiter.loginID) shareTableData.loginID=dineStore.operateWaiter.loginID;
                        dineStore.shareTable(shareTableData);
                      }else {
                        message.info('就餐人数不能为空');
                      }
                    }}>拼台</div>
                    <div className="btn-active" onClick={() => {
                      if(this.state.customerNumber) {
                        let shareTableData={
                          tableID: dineStore.selectedTableList[0].tableID,
                          customerNumber: this.state.customerNumber
                        }
                        //如果选择了操作服务员就追加loginID
                        if(dineStore.operateWaiter.loginID) shareTableData.loginID=dineStore.operateWaiter.loginID;
                        dineStore.shareTable(shareTableData);

                        dineStore.saveCache();
                        browserHistory.push('/dishes/'+dineStore.selectedTableList[0].tableID+'/'+dineStore.selectedTableList[0].subOrderID);
                      }else {
                        message.info('就餐人数不能为空');
                      }
                    }}>拼台并点菜</div>
                </div>
              </div>
            }

            //基础窗口
            return <div id="desk_info_container">
              <h4 id="desk_info_header">桌台信息</h4>
              {selectDesk}
              {orderDetail}
              {waveLine}
              {operate}
              {this.state.statePopup}
            </div>
          }else {
            //空白窗口
            return <div id="desk_info_container">
              <h4 id="desk_info_header">桌台信息</h4>
              <div className="no_message_box">
                  <div className="desk-pic"><i className="iconfont icon-zhuotaixinxi_icon_wuzhuotai-"></i></div>
                  <div className="desk-show-msg">请选择需要操作的桌台</div>
              </div>
            </div>
          }
      })()}
    </div>

  }

}

export default TableWindow;



